$firstColor : black;    /* you can put every color you want ( for background )*/
$primaryColor : whitesmoke;   /* you can put every color you want ( for color)*/

* { 
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    user-select: none;
    transition: all .5s;}

.App {
    width: 60%;
    height: 500px;
    margin: 3rem auto;
    display: flex;}

@mixin flexState($display , $justifyContent) {
    display: $display;
    justify-content: $justifyContent;}

.flexState {
    display: flex;
    flex-direction: column;}

.backgroundState {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;}

.nameState {
    color: $primaryColor;
    font-weight: 500;
    position: relative;
    text-align: center;
    overflow:  hidden;}

.followBtnState {
    width: 40%;
    height: 45px;
    position: relative;
    top: 25%;
    font-size: 1.3rem;
    color: $primaryColor;
    border-radius: 30px;
    border: 2px solid $primaryColor;
    background-color: rgba(255, 255, 255, 0);
    cursor: pointer;
}

.firstState {
    background-color: $firstColor;
    height: 100%;
    width: 80px;
    margin-right: 1%;
    cursor: pointer;
    overflow: hidden;
    @include flexState( flex , space-between );}

.secondState {
    @extend .firstState;
    flex-grow: 1;}

.firstItemUserInDeAct {
    width: 100%;
    height: 100%;
    @extend .flexState;}

.firstItemUserInAct {
    width: 48%;
    height: 100%;
    @extend .flexState;}

.imgUserInDeAct {
    @extend .backgroundState;
    height: 60%;}

.imgUserInAct {
    @extend .backgroundState;
    height: 65%;
    border-bottom-right-radius: 50%;}

.detailUserInDeAct {
    @extend .flexState;
    height: 40%;}
      
.detailUserInAct {
    @extend .flexState;
    height: 35%;
    align-items: center;}

.nameInDeAct {
    @extend .nameState;
    width: 170px;
    transform: rotate(-90deg);
    font-size: 1.8rem;
    top: 41%;
    right: 59%;
}

.nameInAct {
    @extend .nameState;
    width: 100%;
    transform: rotate(0deg);
    top: 10%;
    right: 0%;
    font-size: 2.5rem;
    transition: all 1s;}

.followBtnInDeAct {
    @extend .followBtnState;
    display: none;}

.followBtnInAct {
    @extend .followBtnState;
    display: block;}

.secondItemUserInDeAct, .secondItemUserInAct01 {
    flex-direction: column;
    opacity: 0%;}
      
.secondItemUserInDeAct {
    display: none;}
    
.secondItemUserInAct01, .secondItemUserInAct02 {
    display: flex;
    justify-content: space-between;}

.secondItemUserInAct02 {
    opacity: 100%;
    flex-direction: column;
    width: 48%;}

.detailProfile {
    height: 65px;
    position: relative;
    top: 70%;
    @include flexState( flex , space-around );}

.profItem {
    width: 95px;
    @extend .flexState;}

.number {
    height: 60%;
    color: $primaryColor;
    text-align: center;
    font-size: 1.9rem;
    font-weight: bold;}

.title {
    height: 40%;
    color: $primaryColor;
    text-align: center;}

.posts {
    @extend .backgroundState;
    height: 65%;
    border-top-left-radius: 50%;
    @include flexState( flex , center );}

.goToPageBtn {
    width: 43%;
    height: 46px;
    position: relative;
    top: 76%;
    font-size: 1.2rem;
    color: $primaryColor;
    border-radius: 30px;
    border: 2px solid $firstColor;
    background-color: $firstColor;
    cursor: pointer;
    line-height: 100%}